import {IHeroPack} from "./IHeroPack";
import {HeroModeSelect,HeroCounterPackMode} from "../components";
import HeroRouletteDefendersMode from "../components/heroRouletteDefendersMode";
import HeroRouletteAttackersQueueMode from "../components/heroRouletteAttackersQueueMode";
import HeroAddAttackerMode from "../components/heroAddAttackerMode";
import HeroAddDefenderMode from "../components/heroAddDefenderMode";
import HeroRouletteMode from "../components/heroRouletteMode";

export class MyNavigator {
    static heroModeSelect?:HeroModeSelect
    static heroCounterPackMode?:HeroCounterPackMode
    static heroRouletteDefendersMode?:HeroRouletteDefendersMode
    static heroRouletteAttackersQueueMode?:HeroRouletteAttackersQueueMode
    static heroAddAttackerMode?:HeroAddAttackerMode
    static heroAddDefenderMode?:HeroAddDefenderMode
    static heroRouletteMode?:HeroRouletteMode
    static currentPack:IHeroPack = {hIds:[]}
    static disableClose = true
    static navigate(mode:string,currentPack?:IHeroPack) {
        if (currentPack) {
            MyNavigator.setCurrentPack(currentPack)
        }
        switch (mode) {
            case "RouletteAttackQueue": {
                if (MyNavigator.heroModeSelect) {
                    MyNavigator.heroModeSelect.navigateToTopMode(3,0)
                }
                if (MyNavigator.heroRouletteMode) {
                    MyNavigator.heroRouletteMode.navigateToMode(0)
                }
                if (MyNavigator.heroAddAttackerMode && currentPack) {
                    MyNavigator.heroAddAttackerMode.setHeroPack(currentPack)
                }
                break;
            }
            case "RouletteDefenderQueue": {
                if (MyNavigator.heroModeSelect) {
                    MyNavigator.heroModeSelect.navigateToTopMode(3,2)
                }
                if (MyNavigator.heroRouletteMode) {
                    MyNavigator.heroRouletteMode.navigateToMode(2)
                }
                if (MyNavigator.heroAddDefenderMode && currentPack) {
                    MyNavigator.heroAddDefenderMode.setHeroPack(currentPack)
                }
                break;
            }
            case "CounterPack": {
                if (MyNavigator.heroModeSelect) {
                    MyNavigator.heroModeSelect.navigateToTopMode(0)
                }
                if (MyNavigator.heroCounterPackMode && currentPack) {
                    MyNavigator.heroCounterPackMode.updateHeroIds(currentPack)
                }
                break;
            }
        }
    }
    static setCurrentPack(currentPack:IHeroPack) {
        MyNavigator.currentPack=currentPack
    }
}