import React from "react";
import {HeroCounterPackMode} from "./index";
import {Box, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import HeroOneFightMode from "./heroOneFightMode";
import HeroRouletteMode from "./heroRouletteMode";
import {MyNavigator} from "../types/MyNavigator";
import {HeroFAQ} from "./heroFAQ";
import {HeroBestPackMode} from "./heroBestPackMode";

interface IProps {
    currentMode: number
}

interface IState {
    currentMode: number,
    rouletteSubMode: number
}

export class HeroModeSelect extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            currentMode: props.currentMode,
            rouletteSubMode: 0
        }
        this.setMode=this.setMode.bind(this)
        this.navigateToTopMode=this.navigateToTopMode.bind(this)
        MyNavigator.heroModeSelect=this
    }
    navigateToTopMode(newMode:number,rouletteSubMode?:number) {
        if (rouletteSubMode) {
            this.setState({currentMode: newMode,rouletteSubMode:rouletteSubMode})
        } else {
            this.setState({currentMode: newMode})
        }

    }
    setMode(e:any,value:string) {
        this.navigateToTopMode(~~value)
    }
    renderMode(currentMode:number) {
        if (currentMode===0) {
            return (<HeroCounterPackMode heroIds={MyNavigator.currentPack} currentMode={0} key={MyNavigator.currentPack.hIds.join(',')}/>)
        } else if (currentMode===2) {
            return (<HeroOneFightMode navigateToTopFunction={this.navigateToTopMode} currentMode={1}/>)
        } else if (currentMode===3) {
            return (<HeroRouletteMode navigateFunction={this.navigateToTopMode} currentMode={this.state.rouletteSubMode} key={10+this.state.rouletteSubMode}/>)
        } else if (currentMode===4) {
            return (<HeroFAQ/>)
        }else if (currentMode===5) {
            return (<HeroBestPackMode heroesFilter={[]} currentMode={0}/>)
        }
        else {
            return (null)
        }
    }
    render() {
        return (
            <Box sx={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "flex-start",
                alignItems: "center",
            }}>
            <ToggleButtonGroup
                color="secondary"
                value={""+this.state.currentMode}
                exclusive
                onChange={this.setMode}
            >
                <ToggleButton value="4" color="primary">FAQ</ToggleButton>
                <ToggleButton value="0" color="primary">Find pack</ToggleButton>
                <ToggleButton value="2" color="primary">One fight</ToggleButton>
                <ToggleButton value="3" color="primary">Roulette</ToggleButton>
                <ToggleButton value="5" color="primary">Best packs</ToggleButton>
                    </ToggleButtonGroup>
                <Divider />
                {this.renderMode(this.state.currentMode)}
            </Box>
        )
    }
}

export default HeroModeSelect